/* CART & CHECKOUT PAGES */

/* Hero Sections */
.cart-hero,
.checkout-hero {
  position: relative;
  height: 35vh;
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #442D1C 0%, #652810 50%, #8E5022 100%);
  overflow: hidden;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, rgba(0,0,0,0.3) 0%, rgba(0,0,0,0) 50%, rgba(0,0,0,0.3) 100%);
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
}

.hero-title {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.japanese-accent {
  font-size: 1.5rem;
  font-weight: 300;
  opacity: 0.9;
  letter-spacing: 4px;
}

.main-title {
  font-size: 3.5rem;
  font-weight: 600;
  letter-spacing: 2px;
}

.hero-subtitle {
  margin-top: 12px;
  font-size: 1.2rem;
  opacity: 0.95;
  font-weight: 300;
}

/* Empty Cart */
.cart-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px 40px;
  text-align: center;
  min-height: 60vh;
}

.cart-empty h2 {
  margin-top: 24px;
  font-size: 2rem;
  color: #442D1C;
}

.cart-empty p {
  margin-top: 12px;
  color: #666;
  font-size: 1.1rem;
}

.btn-primary {
  margin-top: 32px;
  padding: 14px 32px;
  background-color: #652810;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary:hover {
  background-color: #8E5022;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(101, 40, 16, 0.3);
}

/* Cart Content */
.cart-content,
.checkout-content {
  padding: 60px 40px;
  max-width: 1400px;
  margin: 0 auto;
}

.cart-container,
.checkout-container {
  display: grid;
  grid-template-columns: 1fr 380px;
  gap: 40px;
  align-items: start;
}

/* Cart Items Section */
.cart-items {
  background: white;
  border-radius: 12px;
  padding: 32px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 20px;
  border-bottom: 2px solid #f0f0f0;
}

.cart-header h2 {
  font-size: 1.8rem;
  color: #442D1C;
}

.btn-text {
  background: none;
  border: none;
  color: #652810;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.btn-text:hover {
  background-color: #f5f5f5;
  color: #8E5022;
}

/* Individual Cart Item */
.cart-item {
  display: grid;
  grid-template-columns: 100px 1fr auto auto;
  gap: 24px;
  align-items: center;
  padding: 24px 0;
  border-bottom: 1px solid #f0f0f0;
}

.cart-item:last-child {
  border-bottom: none;
}

.item-image {
  width: 100px;
  height: 100px;
  border-radius: 8px;
  overflow: hidden;
  background-color: #f5f5f5;
}

.item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.item-details h3 {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 8px;
}

.workshop-details {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid #f0f0f0;
}

.workshop-slot-info {
  font-size: 0.85rem;
  color: #666;
  margin: 4px 0;
  display: flex;
  align-items: center;
  gap: 6px;
}

.item-price {
  color: #666;
  font-size: 0.95rem;
}

/* Quantity Controls */
.item-quantity {
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: #f8f8f8;
  padding: 8px 12px;
  border-radius: 8px;
}

.quantity-fixed {
  min-width: 80px;
  text-align: center;
  font-weight: 600;
  color: #652810;
  font-size: 0.9rem;
}

.quantity-btn {
  background-color: white;
  border: 1px solid #ddd;
  width: 28px;
  height: 28px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #652810;
}

.quantity-btn:hover {
  background-color: #652810;
  border-color: #652810;
  color: white;
}

.quantity {
  min-width: 30px;
  text-align: center;
  font-weight: 600;
  color: #333;
}

/* Item Total */
.item-total {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
}

.total-price {
  font-size: 1.15rem;
  font-weight: 600;
  color: #442D1C;
}

.remove-btn {
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  padding: 6px;
  border-radius: 6px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-btn:hover {
  background-color: #ffebee;
  color: #d32f2f;
}

/* Cart Summary */
.cart-summary,
.checkout-summary {
  background: white;
  border-radius: 12px;
  padding: 32px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  position: sticky;
  top: 100px;
}

.cart-summary h2,
.checkout-summary h2 {
  font-size: 1.5rem;
  color: #442D1C;
  margin-bottom: 24px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  color: #666;
}

.summary-row.total {
  font-size: 1.3rem;
  font-weight: 700;
  color: #442D1C;
  padding: 20px 0 0 0;
}

.summary-divider {
  height: 1px;
  background-color: #e0e0e0;
  margin: 16px 0;
}

.checkout-btn,
.pay-btn {
  width: 100%;
  padding: 16px;
  margin-top: 24px;
  background-color: #652810;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.checkout-btn:hover,
.pay-btn:hover {
  background-color: #8E5022;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(101, 40, 16, 0.3);
}

.continue-shopping-btn {
  width: 100%;
  padding: 14px;
  margin-top: 12px;
  background-color: transparent;
  color: #652810;
  border: 2px solid #652810;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.continue-shopping-btn:hover {
  background-color: #652810;
  color: white;
}

/* Checkout Form */
.checkout-form-section {
  background: white;
  border-radius: 12px;
  padding: 40px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.checkout-form-section h2 {
  font-size: 1.8rem;
  color: #442D1C;
  margin-bottom: 32px;
  padding-bottom: 16px;
  border-bottom: 2px solid #f0f0f0;
}

.checkout-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 0.95rem;
  font-weight: 600;
  color: #442D1C;
}

.form-group input,
.form-group textarea {
  padding: 12px 16px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
  transition: all 0.2s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #652810;
  box-shadow: 0 0 0 3px rgba(101, 40, 16, 0.1);
}

.form-group input.error,
.form-group textarea.error {
  border-color: #d32f2f;
}

.error-message {
  font-size: 0.85rem;
  color: #d32f2f;
  margin-top: -4px;
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

/* Checkout Summary Items */
.summary-items {
  max-height: 300px;
  overflow-y: auto;
  margin-bottom: 16px;
  padding-right: 8px;
}

.summary-items::-webkit-scrollbar {
  width: 6px;
}

.summary-items::-webkit-scrollbar-track {
  background: #f5f5f5;
  border-radius: 3px;
}

.summary-items::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 3px;
}

.summary-items::-webkit-scrollbar-thumb:hover {
  background: #999;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #f0f0f0;
}

.summary-item:last-child {
  border-bottom: none;
}

.item-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.item-name {
  font-weight: 500;
  color: #333;
  font-size: 0.95rem;
}

.item-qty {
  font-size: 0.85rem;
  color: #999;
}

.item-price {
  font-weight: 600;
  color: #442D1C;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .cart-container,
  .checkout-container {
    grid-template-columns: 1fr;
  }

  .cart-summary,
  .checkout-summary {
    position: static;
  }
}

@media (max-width: 768px) {
  .cart-content,
  .checkout-content {
    padding: 40px 20px;
  }

  .main-title {
    font-size: 2.5rem;
  }

  .japanese-accent {
    font-size: 1.2rem;
  }

  .cart-item {
    grid-template-columns: 80px 1fr;
    gap: 16px;
  }

  .item-quantity,
  .item-total {
    grid-column: 2;
  }

  .item-total {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .cart-items,
  .checkout-form-section {
    padding: 24px;
  }

  .form-row {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .main-title {
    font-size: 2rem;
  }

  .item-image {
    width: 70px;
    height: 70px;
  }

  .cart-item {
    grid-template-columns: 70px 1fr;
  }
}
